button {
    border: none;
    background-color: rgb(255, 255, 255);
    font-size: 1.3rem;
    color: #001b2b;
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    border: 0.1px solid  rgb(92, 92, 92);

    outline: none;
  }
  
  button:hover {
    background-color: #cecece;
    color: #000000;
  }
  
  .equals {
    grid-column: 3 / 5;
    background-color: rgb(240, 119, 6);
    color: rgb(247, 243, 238);
  }
  
  .equals:hover {
    background-color:  rgb(231, 121, 17);
    color: rgb(247, 243, 238);

  }
  